    font-weight: 700
}

.select-groups>.select-group,.select-subgroups>.select-group,.subgroup-item,.select-subgroup {
    color: #787e8c;
    width: calc(33% - 12px);
    margin: 10px 5px 0;
    border-radius: 5px;
    border: 1px solid #d8d8da;
    cursor: pointer;
    position: relative
}

.subgroup-item {
    border: unset;
    width: calc(33% - 10px)
}

.subgroup-item .select-group {
    border-radius: 5px;
    border: 1px solid #d8d8da;
