}

.select-group.disabled .select-group__content {
    opacity: .4
}

.select-group.disabled:not(.select-min_plan) {
    pointer-events: none
}

.select-group.active {
    opacity: 1;
    border: 1px solid #c2d7ef;
    color: #3e7ffc
}

.select-group.active .select-group__header {
    background: rgba(62,127,252,.2)
}

.select-group__header,.select-subgroup__header {
    text-align: center;
    padding: 5px;
    font-weight: 700;
    background: rgba(62,127,252,.03);
    display: flex;
    flex-flow: column;
    justify-content: center;
    min-height: 40px
}

.select-group__selector {
    position: relative;
    font-size: .8em;
    text-align: center
}

.select-group__selector select {
    display: none
}

.select-hide {
    display: none
}

.select-group__options {
    margin-top: 5px;
    position: absolute;
    border: 1px solid #c2d7ef;
    background: #fff;
    max-height: 176px;
    left: 0;
    right: 0;
    z-index: 5;
    border-radius: 5px;
    animation: appear both .3s;
    box-shadow: inset 0px 0px 40px 10px rgba(194,215,239,.1);
    display: none;
    transition: max-height 1s ease-out;
    max-height: 0
}

.select-group__options:after {
    content: '';
    width: 0;
    height: 0;
    display: block;
    position: absolute;
    z-index: 10;
    border: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    margin-left: -10px;
    right: 10px;
    border-bottom: 10px solid #c2d7ef;
    top: -10px
}

.select-group__options.active {
    display: block;
    max-height: 300px;
    transition: max-height 1s ease-out
}

.select-group__option {
    position: relative;
    padding-bottom: 1px;
    border-bottom: 1px solid #c2d7ef;
    padding: 5px 15px 5px 10px;
    font-size: .9em;
    cursor: pointer
}

.select-group__option:hover {
    background: rgba(0,0,0,.02)
}

.select-group__option-info {
    display: block;
    position: relative;
    margin: 0 10px;
    float: right;
    color: rgba(0,0,0,.2);
    font-size: .8em;
    border-radius: 50%;
    border: 1px solid rgba(0,0,0,.1);
    width: 16px;
    height: 16px;
    display: flex;
    justify-content: center;
    align-items: center
}

.select-group__option-info:hover {
    color: #fff;
    background: #00b19a;
    text-decoration: none;
    opacity: .6
}

.select-group__option[data-selected=true] {
    display: none
}

.select-group__selected {
    padding: 5px 15px;
    border-top: 1px solid #c2d7ef;
