.select-group__hide-selector .select-group__content {
    height: 100%
}

.select-group__hide-selector .select-group__header {
    height: 100%;
    padding: 0;
    display: flex;
    flex-flow: column;
    justify-content: center
}

.select-group__hide-selector .select-group__selector {
    display: none
}

.select-group .arrow {
    position: absolute;
    top: 5px;
    right: 3px;
    cursor: pointer;
    width: 16px
}

.select-group .arrow-left {
    position: absolute;
    background-color: transparent;
    top: 10px;
    left: 0;
    width: 8px;
    height: 1px;
    display: block;
    transform: rotate(35deg);
    float: right;
    border-radius: 2px
}

.select-group .arrow-left:after {
    content: "";
    background-color: #337ab7;
    width: 100%;
    height: 100%;
    display: block;
    float: right;
    border-radius: 6px 10px 10px 6px;
    transition: all .5s cubic-bezier(.25,1.7,.35,.8);
    z-index: -1
}

.select-group .arrow-right {
    position: absolute;
    background-color: transparent;
    top: 10px;
    left: 6px;
    width: 8px;
    height: 1px;
    display: block;
    transform: rotate(-35deg);
    float: right;
    border-radius: 2px
}

.select-group .arrow-right:after {
    content: "";
    background-color: #337ab7;
    width: 100%;
    height: 100%;
    display: block;
    float: right;
    border-radius: 10px 6px 6px 10px;
    transition: all .5s cubic-bezier(.25,1.7,.35,.8);
    z-index: -1
}

.select-group .arrow.active .arrow-left:after {
    transform-origin: center center;
    transform: rotate(-70deg)
}

.select-group .arrow.active .arrow-right:after {
    transform-origin: center center;
    transform: rotate(70deg)
}

.select-group .disabled {
    display: none !important
}

.select-group__help {
    position: absolute;
    display: flex;
    justify-content: center;
    color: #2281cf;
    width: 20px;
    height: 20px;
    top: 10px;
    right: 10px;
    border-radius: 50%;
    border: 1px solid #2281cf;
    opacity: .4
}

.select-group__help:hover {
    text-decoration: none;
    opacity: 1
}

.select-group .geo:before,.select-subgroup .geo:before {
    display: block;
    content: '';
    position: absolute;
    top: 5px;
    left: 0;
    width: 14px;
    height: 10px;
    background-size: cover;
    background-repeat: no-repeat;
    margin: 0 5px
}

.subgroups {
    position: relative
}

.subgroups {
    padding-top: 40px
}

.groups {
    margin-bottom: 10px;
    padding-bottom: 10px
}

.groups .labels {
    border-bottom: 1px solid #f5f5f5
}

.groups .labels label {
    display: inline-block;
    padding: 5px;
    margin: 0 5px;
    cursor: pointer;
    letter-spacing: .9px;
    font-size: 14px;
    color: #3e7ffc;
    font-weight: 700;
    border-bottom: 2px solid #3e7ffc
}

.groups .labels label {
    color: inherit;
    font-weight: 400;
    border-bottom: none;
    width: auto
}

.groups .labels label.active {
    font-weight: bolder;
    border-bottom: 2px solid #3e7ffc;
    pointer-events: unset
}

.groups .group {
    display: none;
    padding: 5px
}

.groups .group.active {
    display: block
}

.select-subgroup__group {
    display: none
}

.select-subgroup__breadcrumbs {
    display: none;
    position: absolute;
    width: calc(100% - 60px);
    left: 5px;
    top: 5px
}

.subgroups.active .select-subgroup__breadcrumbs {
    display: block
}

.select-subgroup__breadcrumbs li {
    display: inline-block;
    background: rgba(62,127,252,.03);
    padding: 5px 10px;
    border: 1px solid #d8d8da;
    border-radius: 5px;
    cursor: pointer;
    text-align: center
}

.select-subgroup__breadcrumbs li.return {
    width: 30px
}

.select-subgroup__breadcrumbs li.label {
    min-width: calc(33% - 67px)
}

.select-subgroup__breadcrumbs li.active {
    background: rgba(62,127,252,.2);
    border: 1px solid #c2d7ef;
    color: #3e7ffc;
    pointer-events: none
}

.subgroups.active .select-subgroups>.select-group,.subgroups.active .select-subgroups>.select-subgroup {
    display: none
}

.subgroups .group {
    padding: 0;
    width: 100%
}

.subgroups .subgroups-style {
    position: absolute;
    right: 10px;
    top: 10px
}

.subgroups .subgroups-style i.active {
    color: #2281cf
}

.subgroups .subgroups-style i:not(.active) {
    opacity: .4;
    cursor: pointer
}

.subgroups .subgroups-style i:not(.active):hover {
    opacity: .6
}

.subgroups.list .group {
    display: inline-block
}

.subgroups.list .select-subgroups>.select-subgroup {
    display: none
}

.subgroups.list .select-subgroups>.select-group {
    display: block
}

.subgroups.list .subgroup-item {
    display: block;
    position: relative
}

.subgroups.list .select-subgroup__breadcrumbs {
    display: none
}

.subgroup-item {
    display: none
}

.subgroup-item.active {
    display: block
}

.select-subgroup__item-counts {
    position: absolute;
    top: 0;
    right: 5px;
    font-size: .8em
}

.select-group.select-group-auto {
    position: absolute;
    top: -5px
}

.select-group.select-group-auto .select-group__header {
    min-height: unset;
    padding: 5px 0
}

.select-group__content {
    transition: .2s ease
}

.custom-offered-plan-itm {
    transition: .2s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    text-align: center
}

.custom-offered-plan-itm .button-wrapper button {
    background: #3bbd53;
    color: #fff;
    border: 0;
    outline: 0;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    font-weight: 700;
    font-size: 13px;
    line-height: 16px;
    text-transform: uppercase;
    letter-spacing: 1px;
    display: inline;
    padding: 7px 15px;
    -webkit-transition: all .2s;
    -moz-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
    margin: 0 auto
}

.custom-offered-plan-itm .button-wrapper {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 15px 0
}

.select-group.disabled.select-min_plan .custom-offered-plan-itm {
    display: none
}

.select-group.disabled.select-min_plan.active-min .select-group__content {
    opacity: 0
}

.select-group.disabled.select-min_plan.active-min,.select-group.disabled.select-min_plan.active-min .custom-offered-plan-itm {
    opacity: 1;
    display: block
}

.select-group.disabled.select-min_plan .custom-offered-plan-itm h2 {
    font-size: 1.3em
}

.select-group.disabled.select-min_plan .custom-offered-plan-itm a {
    color: #787e8c;
    font-size: .9em;
    padding: 5px;
    display: inline-block;
    text-decoration: none
}


@media (max-width: 1023px) {
    .select-groups>.select-group,.select-subgroups>.select-group,.select-subgroup {
        width:calc(50% - 12px)
    }

    .subgroup-item {
        width: calc(50% - 10px)
    }

    .select-subgroup__breadcrumbs li.label {
        min-width: calc(50% - 58px)
    }
}

@media (max-width: 767px) {
    .groups .labels label {
        font-size:13px
    }
}

