.lnd-s__plan-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
    top: 20px;
    font-size: 0;
    line-height: 0;
    padding: 0;
    margin-bottom: 40px;
    text-align: center
}

.lnd-s__plan-list li {
    display: flex;
    vertical-align: top;
    position: relative
}

.lnd-s__plan-list li:first-child a {
    -webkit-border-radius: 4px 0 0 4px;
    -moz-border-radius: 4px 0 0 4px;
    border-radius: 4px 0 0 4px
}

.lnd-s__plan-list li:first-child a:before {
    -webkit-border-radius: 4px 0 0 4px;
    -moz-border-radius: 4px 0 0 4px;
    border-radius: 4px 0 0 4px
}

.lnd-s__plan-list li:last-child a {
    -webkit-border-radius: 0 4px 4px 0;
    -moz-border-radius: 0 4px 4px 0;
    border-radius: 0 4px 4px 0
}

.lnd-s__plan-list li:last-child a:before {
    -webkit-border-radius: 0 4px 4px 0;
    -moz-border-radius: 0 4px 4px 0;
    border-radius: 0 4px 4px 0
}

.lnd-s__plan-list a {
    position: relative;
    display: block;
    width: 320px;
    text-decoration: none !important;
    padding: 31px 10px 60px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background: #fff;
    -webkit-transition: all .2s;
    -moz-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s
}

.lnd-s__plan-list a:hover {
    text-decoration: none
}

.lnd-s__plan-list a:hover:before {
    border-width: 4px
}

.lnd-s__plan-list a:before {
    content: '';
    -webkit-transition: all .2s;
    -moz-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    position: absolute;
    border: 1px solid #d8d8da;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0
}

