    bottom: 0;
    right: 0;
    z-index: 1070;
    overflow: auto;
    filter: alpha(opacity=100);
    background: rgba(0,0,10,.6)
}

.popup.in {
    opacity: 0;
    filter: alpha(opacity=0)
}

.popup__inner {
    position: relative;
    float: none;
    -webkit-box-shadow: 0 1px 8px rgba(0,0,0,.25);
    -moz-box-shadow: 0 1px 8px rgba(0,0,0,.25);
