    height: 15px;
    font-size: 30px;
    color: #9a9696;
    z-index: 1;
    cursor: pointer;
    display: block
}

.popup__close-btn:before,.popup__close-btn:after {
    content: '';
    display: block;
    width: 100%;
    height: 2px;
    border-radius: 20px;
    background: #0086e4;
    position: absolute
}

.popup__close-btn:before {
    -webkit-transform: rotate(-135deg);
    -moz-transform: rotate(-135deg);
    -o-transform: rotate(-135deg);
    transform: rotate(-135deg)
}

.popup__close-btn:after {
    -webkit-transform: rotate(135deg);
    -moz-transform: rotate(135deg);
    -o-transform: rotate(135deg);
    transform: rotate(135deg);
    top: 0
}

