.tremor {
    animation: tremor 1s ease-in-out
}

@keyframes tremor {
    0% {
        -webkit-transform: rotate(4deg);
        transform: rotate(4deg)
    }

    25% {
        -webkit-transform: rotate(-4deg);
        transform: rotate(-4deg)
    }

    50% {
        -webkit-transform: rotate(3deg);
        transform: rotate(3deg)
    }

    75% {
        -webkit-transform: rotate(-1deg);
        transform: rotate(-1deg)
    }
}

